/**
 * List 列表
 * @description 列表组件
 * @tutorial https://ext.dcloud.net.cn/plugin?id=24
 */
export default {
	name: 'UniList',
	'mp-weixin': {
		options: {
			multipleSlots: false
		}
	},
	props: {
		enableBackToTop: {
			type: [Boolean, String],
			default: false
		},
		scrollY: {
			type: [Boolean, String],
			default: false
		}
	},
	provide() {
		return {
			list: this
		}
	},
	created() {
		this.firstChildAppend = false
	},
	methods: {
		loadMore(e) {
			this.$emit("scrolltolower");
		}
	}
}
