export default {
	name: "showTips",
	data() {
		return {
			isFirst: true, //是否首次
			showTips: false, //是否展示
		};
	},

	onReady() {
		const _that = this;
		//获取本地缓存
		let cache = uni.getStorageSync('isFirst');

		if (cache) {
			_that.showTips = false;
		} else {
			_that.showTips = true;
			uni.setStorage({
				key: 'isFirst',
				data: '0'
			})
			setTimeout(() => {
				_that.showTips = false;
			}, 5000); //5s后消失
		}
	},

	methods: {
		//手动关闭
		closeTips() {
			const _that = this;
			_that.showTips = false;
			uni.setStorage({
				key: 'isFirst',
				data: '0'
			})
		}
	}


}
